/* ----- */





.floatingDivForIntroRight {
  display: flex;
  align-items: center;

  padding: 0px 18px 0px 0px;
  height: 4.5rem;
  cursor: pointer;
  transition: transform 0.3s; /* Animation */
}
.floatingDivForIntroRight:hover {
  transform: scale(1.05);

  filter: saturate(200%) brightness(100%);
}
.floatingDivForIntroRight > img {
