}

/* Media query for mobile devices */
@media (max-width: 767px) {
  .floatingDivForMenu {
    transform: scale(0.6);
  }

 

  .floatingDiv {
    transform: scale(0.6);
  }

  .floatingDivForIntroRight {
    transform: scale(0.6);
  }

  .floatingDivForLogo {
    justify-content: left;
    margin-top: 2rem;
    margin-left: -15rem;
    display: flex;
    align-items: center;
    text-align: center;
    transform: scale(0.6);
    height: 2.5rem;
    cursor: pointer;
    transition: transform 0.3s; /* Animation */
  }
  .floatingDivForLogo:hover {
    transform: scale(0.61);
  }
  .floatingDivForLogo > img {
    transform: scale(0.61);
  }
}
