.container-istatistik {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  align-items: center;
  margin-top: -4rem;
  
}

.left-column {
  width: 100%;
  margin-right: 8rem;
  
 
}

.right-column {
  width: 150%;
  margin-left: 3rem;
  margin-top: -12rem;

}

.istatistik-card {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  max-width: 35rem;
  max-height: auto;
  min-height: 16rem;
  margin:auto;

  background: rgba(255, 255, 255, 0.86);
  border: 7px solid var(--blueCard);
  border-radius: 20px;
  cursor: pointer;
 
  line-height: 1.4;
  object-fit: cover;
  white-space: normal;
  overflow: hidden;
  padding: 2vh;
  
}


.istatistik-card-text {
  text-align: left;
  font-size: 1rem;
}

.swiper-button-prev,
.swiper-button-next {
  position: absolute;
  top: 50%;
}

.swiper-button-prev {
  
  margin-left: -4rem;
}

.swiper-button-next {
  
  margin-right: -4rem;
}

@media screen and (max-width: 480px) {
  .left-column {
    margin-top: -20rem;
  }
  .right-column {
    margin-top: -12rem;
  }

}




@media only screen and (min-width: 768px) {
  .container-istatistik {
    flex-direction: row;
    justify-content: space-between;
    align-items: flex-start;
    margin-top: -2rem;
  }

  .left-column {
    width: calc(50% - 2rem);
    order: 1;
  
  }
  .right-column {
    width: calc(50% - 2rem);
    order: 2;
    margin-top: 2rem;
    margin-left: -8rem;
  }

  .istatistik-card{
    width: 100%;
    
}
}

@media only screen and (min-width: 1023px) {
  .container-istatistik {
    flex-direction: row;
    justify-content: space-between;
    align-items: flex-start;
    margin-top: -4rem;
  }

  .left-column {
    width: calc(50% - 2rem);
    order: unset;
    
 
  }

  .right-column {
    width: calc(50% - 2rem);
    order: unset;
   
  }
}


